/***************
  Typography 
 ***************/
h1,
h2,
h3,
h4,
h5,
h6,
body,
a,
span,
p,
ul,
li,
* {
  -webkit-font-smoothing: antialiased !important;
  -moz-osx-font-smoothing: grayscale !important; }

html {
  overflow-x: hidden;
  overflow-y: scroll !important;
  scroll-behavior: auto !important; }

body {
  font-family: "Barlow", sans-serif;
  color: #000;
  overflow: hidden;
  -ms-overflow-style: none;
  position: relative;
  top: 0;
  left: 0; }
  body.no-overflow {
    overflow: unset; }
  body.scroll-lock, body.lock-scroll {
    overflow: hidden;
    position: fixed;
    width: 100%; }
  body.lockscroll {
    width: 100vw;
    height: 100vh;
    position: fixed; }
  body.modal-open {
    padding-right: 0 !important; }
  body .modal-backdrop.fade {
    transition: all 0.35s; }
  body .modal-backdrop.show {
    opacity: 1;
    background: rgba(0, 0, 0, 0.25);
    backdrop-filter: blur(4px); }
  body .fancybox-slide--html {
    padding: 0; }
  body section {
    z-index: 2; }
  body .content-area {
    position: relative;
    width: 1280px;
    max-width: 100%;
    padding: 0 20px;
    margin-left: auto;
    margin-right: auto; }
  body a,
  body button,
  body input,
  body select,
  body textarea {
    transition: all 0.35s; }
    body a:focus, body a:hover,
    body button:focus,
    body button:hover,
    body input:focus,
    body input:hover,
    body select:focus,
    body select:hover,
    body textarea:focus,
    body textarea:hover {
      box-shadow: none !important;
      outline: none !important;
      text-decoration: none;
      transition: all 0.35s; }
  body sup {
    top: -1.7em;
    font-size: 30%; }
  body a {
    color: inherit;
    text-decoration: none; }
    body a:hover {
      color: inherit; }
  body .barlow_semi {
    font-family: "Barlow Semi Condensed", sans-serif !important; }
  body p {
    font-family: "Barlow", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 150%; }
  body h1 {
    font-family: "Barlow Condensed", sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 104px;
    line-height: 102%; }
  body h2 {
    font-family: "Barlow Condensed", sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 52px;
    line-height: normal; }
  body h3 {
    font-family: "Barlow Condensed", sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 38px;
    line-height: normal; }
  body h4 {
    font-family: "Barlow Condensed", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 12px;
    line-height: 17px;
    color: #777777; }
  body strong {
    font-weight: 700; }
  body .btn {
    font-family: "Barlow Condensed", sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 17px;
    line-height: normal;
    text-align: center;
    letter-spacing: 0.15em;
    text-transform: uppercase;
    color: #fff;
    background: linear-gradient(90deg, #288648 0%, #2aaf56 100%);
    border: 1px solid transparent;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: 13px 21px;
    transition: all 0.35s;
    border-radius: 4px; }
    body .btn.large {
      font-size: 21px; }
    body .btn.btn-primary {
      color: #fff;
      background: linear-gradient(90deg, #288648 0%, #2aaf56 100%);
      border: 1px solid transparent; }
      body .btn.btn-primary:hover {
        background: linear-gradient(90deg, #2AAF56 0%, #2AAF56 100%);
        transition: all 0.35s; }

header {
  background-color: #fff;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 9;
  transition: all .6s; }
  header.scrolled {
    transform: translateY(-110px); }
  header .header-container {
    width: 1200px;
    max-width: 100%;
    padding: 19px 0px;
    margin-left: auto;
    margin-right: auto; }

footer {
  margin-top: 36px;
  padding-bottom: 24px; }
